#About{
    background-color: #ffffff;
    min-height: 100vh;

    h2{
        color: #ececec;
        font-size: 14vw;
        font-family: Handwriting;

        @media (max-width: 575.98px) {
            font-size: 24vw;
        }
    }

    p{
        position:relative;
        top: -20vh;
        left: 40vh;
        width: 120vh;
        font-size: 2vh;
        text-align: justify;

        @media (max-width: 575.98px) {
            left: 0;
            width: 100%;
            top: -4vh;
            font-size: 1.5vh;
        }
    }
}