#Skills{
    background-color: #000000;
    min-height: 100vh;

    h2{
        color: rgba(250, 250, 250, 0.1);
        font-size: 14vw;
        font-family: Handwriting;
        text-align: right;

        @media (max-width: 575.98px) {
            font-size: 24vw;
            text-align: left;
        }
    }

    .skillsText{
        position:relative;
        top: -20vh;
        left: 0;
        width: 140vh;
        font-size: 3vh;
        text-align: justify;
        color: #ffffff;
        font-family: Text;

        @media (max-width: 575.98px) {
            font-size: 2vh;
            top: -2vh;
        }
    }

    .skill{
        text-align: center;
        color: #ffffff;
        font-family: "Text";

        @media (max-width: 575.98px) {
            font-size: 3vw;
        }
        
        h3{
            font-family: "Titles";
            font-size: 2vw;
            color: yellowgreen;
            margin: 0;

            @media (max-width: 575.98px) {
                font-size: 4vw;
            }
        }
    }
}